body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.switch-wrapper {
  position: relative;
  width:100%
}
 
.switch-wrapper > div {
  position: absolute;
  width:100%
}
nav{
    display: -webkit-flex;
    display: flex;
    background: #30302D
}

nav a{
    -webkit-flex: 1 0 auto;
            flex: 1 0 auto;
    padding:15px 0px;
    text-align: center;
    text-decoration: none;
    color: #F1EDEE
}

.selected{
    background: #DB5461;
}
.hisItem{
    margin:5px;
    padding:5px;
    border: 1px solid #DB5461;
    border-radius: 2px;
    color: #DB5461;
  }
  
.hisItem .header{
    font-size:10px;
    font-weight: bold
  }
  
.hisItem .data{
    float:right
  }
  
.hisItem .body{
    display: -webkit-flex;
    display: flex;
  }
  
.hisItem .hisProduto{
    -webkit-flex: 1 1;
            flex: 1 1
  }
#ativo{
  background: #DB5461
}
#desativado{
  background: #8AA29E
}
#dark{
  background: #30302D
}
#light{
  background: #F1EDEE
}
#primary{
  background: #3D5467
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

h2{
  margin:10px 0 0 5px;
  font-size:16px;
  color: #999999
}

.name-control{
  width: 90%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin:5px;
  padding:5px;
}

.name-control span{
  font-size:10px;
  color: #30302D;
}

.name-control input{
  font-size:16px;
  color: #30302D;
  border: none;
  border-bottom: 1px solid #30302D;
}

.btn{
  display: -webkit-flex;
  display: flex;
}

.btn button{
  background: #30302D;
  border-radius: 10px;
  padding:20px 25px;
  color: #FFFFFF;
  border:none;
  -webkit-flex: 1 1;
          flex: 1 1;
  margin:5px
}

.optbtn{
  float:right;
  margin-right:10px;
  border-radius: 10px;
  padding:10px 20px;
  color: #FFFFFF;
  border:none;
  font-size:14px
}

.red{
  background: #af5c0e!important;
}

.green{
  background: #059960;
}

.mb2{
  margin-bottom: 20px
}
.MainScreen{
    padding-top:20px
}
  
.blur{
    -webkit-filter: blur(2px);
            filter: blur(2px);
}

.ProdutoForm{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.ProdutoForm div{
    padding:5px;
    margin: 5px;
}

.ProdutoForm .form-control{
    background: #F1EDEE;
    
    -webkit-flex: 2 1;
    
            flex: 2 1;
    box-shadow: 0px 0px 1px 0px rgba(0,0,0,0.45);
}

.ProdutoForm span{
    color: #DB5461;
    display: block;
    font-size:10px
}
.ProdutoForm input{
    width:75%;
    border-radius: 5px;
    border: 1px solid #30302D;
    color: #30302D;
    font-weight: bold;
    font-size:16px;
    padding:5px;
}

.Produto{
    margin:5px;
    -webkit-flex: 1 1;
            flex: 1 1;
    border-radius:10px;
    box-shadow: 0px 0px 1px 0px rgba(0,0,0,0.45);
    -webkit-animation-name: aniLista;
            animation-name: aniLista;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
  
  }
  
  .Produto .header{
    font-size: 10px;
    background: #30302D;
    color: #DB5461;
    padding:5px;
    font-style: italic;
  }
  
  .Produto .body{
    font-size:24px;
    background: #DB5461;
    color: #30302D;
    padding:5px
  }
  
  .Produto .info{
    font-size:12px
  }
  
  .Produto .result{
    float:right
  }

  .lista{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column
  }
  
  
  
  .ListaForm{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-animation-name: aniDialogo;
            animation-name: aniDialogo;
    -webkit-animation-duration: .5s;
            animation-duration: .5s
  }
  
  .ListaForm .wrapper{
    border: 1px solid #af5c0e;
    -webkit-flex: 1 1;
            flex: 1 1;
    margin: auto 5%;
    background: #FFFFFF;
    position: absolute;
    width: 80%;
    padding: 5%;
    z-index:1;
    border-radius: 5px
  }
  
  .ListaForm .bg{
    width:100%;
    height:100%;
    position: absolute;
    top:0px;
    left:0px;
    background: #999999;
    opacity: 0.5;
  }

  @-webkit-keyframes aniLista{
    from{ opacity: 0; -webkit-transform:translateY(-10px); transform:translateY(-10px)}
    to{ opacity : 1; -webkit-transform:translateY(0px); transform:translateY(0px)}
  }

  @keyframes aniLista{
    from{ opacity: 0; -webkit-transform:translateY(-10px); transform:translateY(-10px)}
    to{ opacity : 1; -webkit-transform:translateY(0px); transform:translateY(0px)}
  }
  
  @-webkit-keyframes aniDialogo{
    from{ opacity: 0; -webkit-transform:scale(0.2); transform:scale(0.2)}
    to{ opacity : 1; -webkit-transform:scale(1); transform:scale(1)}
  }
  
  @keyframes aniDialogo{
    from{ opacity: 0; -webkit-transform:scale(0.2); transform:scale(0.2)}
    to{ opacity : 1; -webkit-transform:scale(1); transform:scale(1)}
  }
